// the root, initial state object
const state = {
  name: '',
  imgurl: '',
  userType: null,
  menuAdminShow: true,
  menuUserShow: true
}

// define the possible mutations that can be applied to our state
const mutations = {
  set_userName (state, obj) {
    state.name = obj
  },
  set_imgurl (state, obj) {
    state.imgurl = 'http://localhost:3000/uploads/images/' + obj
  },
  set_userType (state, obj) {
    state.userType = obj
  },
  set_right (state, obj) {
    if (obj === '1') {
      state.menuAdminShow = true
      state.menuUserShow = false
    } else if (obj === '2') {
      state.menuAdminShow = false
      state.menuUserShow = true
    }
  },
  clear_state () {
    state.imgurl = null
  }
}

// create the Vuex instance by combining the state and mutations objects
// then export the Vuex store for use by our components
export default new Vuex.Store({
  state,
  mutations
})



