<template>
  <div>
    <!-- START BREADCRUMB -->
    <ul class="breadcrumb" style="text-align:left;">
      <li><a>用户</a></li>
      <li class="active">用户主页</li>
    </ul>
    <!-- END BREADCRUMB -->

    <!-- PAGE CONTENT WRAPPER -->
    <div class="page-content-wrap">

      <!-- START WIDGETS -->
      <div class="row">
        <div class="col-md-3">

          <!-- START WIDGET SLIDER -->
          <div class="widget widget-default widget-item-icon">
            <div class="widget-item-left">
              <span class="fa fa-asterisk"></span>
            </div>
            <div class="widget-data">
              <div class="widget-int num-count">{{AllInfo.RadioactiveLength}}</div>
              <div class="widget-title">放射源信息数</div>
              <div class="widget-subtitle">在这个网站上</div>
            </div>
            <div class="widget-controls">
              <a class="widget-control-right widget-remove" data-toggle="tooltip" data-placement="top" title="移除"><span class="fa fa-times"></span></a>
            </div>
          </div>
          <!-- END WIDGET SLIDER -->

        </div>
        <div class="col-md-3">

          <!-- START WIDGET MESSAGES -->
          <div class="widget widget-default widget-item-icon">
            <div class="widget-item-left">
              <span class="fa fa-flag"></span>
            </div>
            <div class="widget-data">
              <div class="widget-int num-count">{{AllInfo.WorkTextLength}}</div>
              <div class="widget-title">发表文章数</div>
              <div class="widget-subtitle">在这个网站上</div>
            </div>
            <div class="widget-controls">
              <a class="widget-control-right widget-remove" data-toggle="tooltip" data-placement="top" title="移除"><span class="fa fa-times"></span></a>
            </div>
          </div>
          <!-- END WIDGET MESSAGES -->

        </div>
        <div class="col-md-3">

          <!-- START WIDGET REGISTRED -->
          <div class="widget widget-default widget-item-icon">
            <div class="widget-item-left">
              <span class="fa fa-user"></span>
            </div>
            <div class="widget-data">
              <div class="widget-int num-count">{{AllInfo.UserLength}}</div>
              <div class="widget-title">注册使用人数</div>
              <div class="widget-subtitle">在这个网站上</div>
            </div>
            <div class="widget-controls">
              <a class="widget-control-right widget-remove" data-toggle="tooltip" data-placement="top" title="移除"><span class="fa fa-times"></span></a>
            </div>
          </div>
          <!-- END WIDGET REGISTRED -->

        </div>
        <div class="col-md-3">

          <!-- START WIDGET CLOCK -->
          <div class="widget widget-info widget-padding-sm">
            <div class="widget-big-int plugin-clock">00:00</div>
         <!--   <div class="widget-subtitle plugin-date">加载中</div>-->
            <div class="widget-title">{{data}}</div>
            <div class="widget-controls">
              <a class="widget-control-right widget-remove" data-toggle="tooltip" data-placement="top" title="移除"><span class="fa fa-times"></span></a>
            </div>
            <div class="widget-buttons widget-c3">
              <div class="widget-title">当前时间</div>
            </div>
          </div>
          <!-- END WIDGET CLOCK -->

        </div>
      </div>
      <!-- END WIDGETS -->

      <div class="row">

        <div class="col-md-12">

          <!-- START SALES & EVENTS BLOCK -->
          <div class="panel panel-default">
            <div class="panel-heading">
              <div class="panel-title-box">
                <h3>网站相关介绍</h3>
              </div>
            </div>
            <div class="panel-body padding-0">
              <div class="chart-holder" style="margin: 8px">

            <!--    <BarChart style="margin: 10px; "
                  :options="{responsive: false, maintainAspectRatio: false}"
                  :width="1280"
                  :height="380"
                  :chart-data="datacollection"></BarChart>-->
                <md-card-media>
                  <!-- swiper -->
                  <swiper :options="swiperOption">
                    <div class="parallax-bg" slot="parallax-bg" data-swiper-parallax="-23%"
                         style="background-image:url(../../static/img/welcome.jpg)">
                    </div>
                    <swiper-slide>
                      <div class="title" data-swiper-parallax="-100">放射源管理介绍</div>
                      <div class="subtitle" data-swiper-parallax="-200">指南</div>
                      <div class="text" data-swiper-parallax="-300">
                        <p>放射源信息管理模块是用户对放射源信息录入的一个模块。此模块包含放射源信息的增删改查，同时在新增和修改都有进行相关的输入验证。为了方便用户查询，绑定了Enter键，根据负责人名字查询或者是放射源名称查询。同时在数据表格的每一行前面都有一个隐藏或者打开的图片，在打开时，能看到放射源数据的详情以及放射源按时间的放射源图表。</p>
                      </div>
                    </swiper-slide>
                    <swiper-slide>
                      <div class="title" data-swiper-parallax="-100">放射源定位介绍</div>
                      <div class="subtitle" data-swiper-parallax="-200">指南</div>
                      <div class="text" data-swiper-parallax="-300">
                        <p>放射源信息定位模块是用户对放射源信息在地图上展示的一个模块。此模块包含放射源放射地点相关，在放射源信息中有经度和纬度。在知道放射源经纬度的情况下，我们能很容易能在地图中定位到相关位置，并借助相关地图API能迅速将其显示出来。</p>
                      </div>
                    </swiper-slide>
                    <swiper-slide>
                      <div class="title" data-swiper-parallax="-100">公文管理介绍</div>
                      <div class="subtitle" data-swiper-parallax="-200">指南</div>
                      <div class="text" data-swiper-parallax="-300">
                        <p>公文管理模块是用户对相关放射源撰写公文的一个模块。此模块包含文章信息的增删改查, 并且还有私密和公开的文章。在文章管理中，能够对文字进行相关的处理，同时在文章展示中，私密文章将不给予展示，并且能够对相关文章进行评论，如果文章是属于自己的，将能够看见删除按钮，如果不是，则不能删除评论。</p>
                      </div>
                    </swiper-slide>
                    <swiper-slide>
                    <div class="title" data-swiper-parallax="-100">车辆信息介绍</div>
                    <div class="subtitle" data-swiper-parallax="-200">指南</div>
                    <div class="text" data-swiper-parallax="-300">
                      <p>车辆信息管理模块是用户对车辆管理一个模块。此模块包含车辆信息的增删改查, 同时能够对车辆负责人Enter或按钮查询，车辆信息在任务运输时将会与之相关联。</p>
                    </div>
                  </swiper-slide>
                    <swiper-slide>
                      <div class="title" data-swiper-parallax="-100">运输任务介绍</div>
                      <div class="subtitle" data-swiper-parallax="-200">指南</div>
                      <div class="text" data-swiper-parallax="-300">
                        <p>运输任务管理模块是用户对运输任务管理的一个模块。此模块包含运输任务的增删改查, 以及运输任务的详情信息。在运输任务列表中，有灰色（未开始）、绿色（正常）、蓝色（结束）、红色（错误）图标表示任务的状态。在运输任务展示图中，会精确展示每一个在途地点。若是红色的字体，则为错误状态。</p>
                      </div>
                    </swiper-slide>
                    <swiper-slide>
                      <div class="title" data-swiper-parallax="-100">在线视频监控介绍</div>
                      <div class="subtitle" data-swiper-parallax="-200">指南</div>
                      <div class="text" data-swiper-parallax="-300">
                        <p>在线视频监控模块是用户对放射源监控的一个模块。此模块包含一个视频直播（用于户外实时在线监控），一个根据提供的视频地址播放视频，还有一个开启本机摄像头监控的功能。</p>
                      </div>
                    </swiper-slide>
                    <div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>
                    <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
                    <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
                  </swiper>
                </md-card-media>

              </div>
            </div>
          </div>
          <!-- END SALES & EVENTS BLOCK -->

        </div>

        <div class="col-md-12">

          <!-- START PROJECTS BLOCK -->
          <div class="panel panel-default">
            <div class="panel-heading">
              <div class="panel-title-box">
                <h3>网 站</h3>
                <span>网站活跃排行版</span>
              </div>
              <ul class="panel-controls" style="margin-top: 2px;">
                <li><a class="panel-fullscreen"><span class="fa fa-expand"></span></a></li>
                <li><a class="panel-refresh"><span class="fa fa-refresh"></span></a></li>
                <li class="dropdown">
                  <a class="dropdown-toggle" data-toggle="dropdown"><span class="fa fa-cog"></span></a>
                  <ul class="dropdown-menu">
                    <li><a class="panel-collapse"><span class="fa fa-angle-down"></span> Collapse</a></li>
                    <li><a class="panel-remove"><span class="fa fa-times"></span> Remove</a></li>
                  </ul>
                </li>
              </ul>
            </div>
            <div class="panel-body panel-body-table">

              <div class="table-responsive">
                <table class="table table-bordered table-striped">
                  <thead>
                  <tr>
                    <th width="50%">名称</th>
                    <th width="50%">活跃度</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr v-for="item in ShowInfo.ActivityInfo">
                    <td><strong>{{item.name}}</strong></td>
                    <td>
                      <div class="progress progress-small progress-striped active">
                        <div class="progress-bar progress-bar-warning" role="progressbar" data-toggle="tooltip" data-placement="top" :title=item.loginNumber  :aria-valuenow=item.loginNumber aria-valuemin="0" v-bind:aria-valuemax=progressMax v-bind:style="{width:(item.loginNumber/progressMax*100) + '%'}">活跃度{{item.loginNumber}}%</div>
                      </div>
                    </td>
                  </tr>
                  </tbody>
                </table>
              </div>

            </div>
          </div>
          <!-- END PROJECTS BLOCK -->

        </div>

      </div>

      <!-- START DASHBOARD CHART -->
      <div class="chart-holder" id="dashboard-area-1" style="height: 200px;"></div>
      <div class="block-full-width">

      </div>
      <!-- END DASHBOARD CHART -->

    </div>
    <!-- END PAGE CONTENT WRAPPER -->
  </div>
</template>

<script>
  import BarChart from '../assets/js/barChar'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'

  export default {
    components: { BarChart, swiper, swiperSlide },
    data () {
      return {
        ShowInfo: [],
        AllInfo: [],
        progressMax: 0,
        data: null,
/**        datacollection: {
          labels: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
          datasets: [
            {
              label: '用户量',
              borderColor: '#1caf9a',
              data: ['1', '4', '8', '6']
            }
          ]
        },**/
        swiperOption: {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          nextButton: '.swiper-button-next',
          prevButton: '.swiper-button-prev',
          parallax: true,
          speed: 600,
          autoplay: 5000,
          autoplayDisableOnInteraction: false
        }
      }
    },
    mounted () {
      this.getInfo()
      this.setData()
    },
    methods: {
      getInfo () {
        var getUrl = 'http://localhost:3000/welcome/get'
        var resource = this.$resource(getUrl)
        resource.get()
          .then((response) => {
            this.AllInfo = response.body
            this.ShowInfo = this.AllInfo
            this.setProgressMax()
          })
          .catch(function (response) {
            console.log(response)
          })
      },
      setProgressMax () {
        var temp = []
        for (var i = 0; i < this.AllInfo.ActivityInfo.length; i++) {
          temp.push(this.AllInfo.ActivityInfo[i].loginNumber)
        }
        this.progressMax = this.max(temp).toString()
      },
      max (value) {
        var max = null
        for (var i = 0; i < value.length; i++) {
          if (value[i] > max) {
            max = value[i]
          }
        }
        return max
      },
      setData () {
        var myDate = new Date()
        this.data = myDate.toLocaleDateString()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .swiper-slide {
    font-size: 18px;
    color:#fff;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 40px 60px;
    background-color: transparent!important;
    justify-content: space-around!important;
  }
  .parallax-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 130%;
    height: 100%;
    -webkit-background-size: cover;
    background-size: cover;
    background-position: center;
  }
  .swiper-slide .title {
    font-size: 41px;
    font-weight: 300;
  }
  .swiper-slide .subtitle {
    font-size: 21px;
  }
  .swiper-slide .text {
    font-size: 14px;
    max-width: 400px;
    line-height: 1.3;
  }
</style>



