<template>
  <div class="hello">
    <div>
    <!-- START BREADCRUMB -->
      <ul class="breadcrumb">
        <li><a>主页</a></li>
        <li><a>公文</a></li>
       <li class="active">公文展示</li>
      </ul>
      <!-- END BREADCRUMB -->

      <!-- PAGE CONTENT WRAPPER -->
      <div class="page-content-wrap">

      <!-- START CONTENT FRAME TOP -->
      <div class="content-frame-top">
        <div class="page-title">
          <h2><span class="fa fa-file-text-o"></span> 文章查看</h2>
        </div>
      </div>
      <!-- END CONTENT FRAME TOP -->

      <div class="row">
        <div class="col-md-">

          <!-- START TIMELINE FILTER -->
          <div class="panel panel-default">
            <div class="panel-body">
              <h3 class="panel-title col-md-5">
                <input v-model="selectText" class="form-control" @keyup.enter="selectInfo" placeholder="请输入发表者姓名查询"/>
              </h3>
              <div class="btn-group " style="float: left">
                <button class="btn btn-info" @click="selectInfo"><i class="glyphicon glyphicon-zoom-out"></i>查 询</button>
              </div>
            </div>
          </div>

        </div>

      </div>

      <div class="row">
        <div class="col-md-12">

          <!-- START TIMELINE -->
          <div class="timeline timeline-right">

            <!-- START TIMELINE ITEM -->
            <div class="timeline-item timeline-main">
              <div class="timeline-date">Begin</div>
            </div>
            <!-- END TIMELINE ITEM -->

            <!-- START TIMELINE ITEM -->
            <div class="timeline-item timeline-item-right" v-for="item in items">
              <div class="timeline-item-info">{{item.createDate}}</div>
              <div class="timeline-item-icon"><span class="fa fa-globe"></span></div>
              <div class="timeline-item-content">
                <div class="timeline-heading">
                  <img v-bind:src='item.imgUrl' />
                  <a>{{item.author}}</a> 发表于 <a>{{item.createTime}}</a>
                </div>
                <strong>&nbsp;&nbsp;&nbsp;标题：{{item.title}}</strong>
                <div class="timeline-body">
                  <p v-html="item.htmlForEditor">{{item.htmlForEditor}}</p>
                </div>

                <div class="timeline-body comments" v-for="tempComment in item.comment">
                  <div class="comment-item">
                    <p class="comment-head">
                      <a @click="answerDialogShow(tempComment,item)">{{tempComment.commenterName}} <span class="text-muted">&nbsp;回复</span>&nbsp;{{tempComment.commentederName}}&nbsp;:&nbsp;&nbsp;<strong>{{tempComment.commentContent}}</strong></a>
                      <a v-if="item.userid === Cookie.userid" class="pull-right" data-toggle="tooltip" data-placement="bottom" title="删除" @click="deleteContent(item,tempComment.commentId)"><span class="fa fa-times"></span></a>
                    </p>
                  </div>
                </div>

                <div class="timeline-footer" style="margin-top: 8px">
                  <a @click="addDialogShow(item)"><strong><span class="fa fa-comments"></span>&nbsp;评 论</strong></a>
                  <a><span class="fa fa-comment pull-right">&nbsp;{{item.comment.length}}</span></a>
                </div>
              </div>
            </div>
            <!-- END TIMELINE ITEM -->

            <!-- START TIMELINE ITEM -->
            <div class="timeline-item timeline-main">
              <div class="timeline-date">End</div>
            </div>
            <!-- END TIMELINE ITEM -->
          </div>
          <!-- END TIMELINE -->

        </div>
      </div>

    </div>
      <!-- END PAGE CONTENT WRAPPER -->

      <!-- 新增 -->
      <div class="dialog" :class="addIsAbleShowClass" role="dialog" style="z-index: 111">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header dialogHeader" >
              <button type="button" class="close" data-dismiss="modal" @click="addDialogClose"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
              <h4 class="modal-title">评论</h4>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-md-4">
                  <div class="icon-preview"></div>
                </div>
                <div class="col-md-12">

                  <form class="form-horizontal" role="form">

                    <div class="form-group">
                      <label class="col-md-2 control-label">内容</label>
                      <div class="col-md-10">
                        <textarea class="form-control" rows="3" v-model="commentContent" v-bind:placeholder="watermark"></textarea>
                      </div>
                    </div>

                    <div class="col-md-offset-5 col-sm-offset-5">
                      <button class="btn btn-info active" :disabled="disabled" @click="addContents"><i class="fa fa-comment-o"></i>发  送</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
      <!-- 新增-->

      <!-- 新增 -->
      <div class="dialog" :class="answerClass" role="dialog" style="z-index: 111">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header dialogHeader" >
              <button type="button" class="close" data-dismiss="modal" @click="answerDialogClose"><span aria-hidden="true">&times;</span><span class="sr-only">Close</span></button>
              <h4 class="modal-title">评论</h4>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-md-4">
                  <div class="icon-preview"></div>
                </div>
                <div class="col-md-12">

                  <form class="form-horizontal" role="form">

                    <div class="form-group">
                      <label class="col-md-2 control-label">内容</label>
                      <div class="col-md-10">
                        <textarea class="form-control" rows="3" v-model="commentContent" v-bind:placeholder="watermark"></textarea>
                      </div>
                    </div>

                    <div class="col-md-offset-5 col-sm-offset-5">
                      <button class="btn btn-info active" :disabled="disabled" @click="anwerContent"><i class="fa fa-comment-o"></i>发  送</button>
                    </div>
                  </form>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
      <!-- 新增-->

      </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import VueResource from 'vue-resource'
  import {getCookie, CookieUserType, CookieName} from '../services/Cookie'
  Vue.use(VueResource)

  export default {
    data: function () {
      return {
        htmlForEditor: '',
        createDate: '',
        createTime: '',
        author: '',
        title: '',
        IsPublic: '',
        createDateTime: '',
        selected: '0',
        options: [
          { text: '公开', value: '0' },
          { text: '私密', value: '1' }
        ],
        items: [],
        textID: '',
        delClass: {
          open: false
        },
        Dic: {},
        selectText: '',
        commentator: '',
        commentContent: '',
        commenteder: '',
        updateContent: '',
        watermark: '说点什么吧...',
        commentClass: {
          disabled: false
        },
        addIsAbleShowClass: {
          modal: true,
          fade: true
        },
        answerClass: {
          modal: true,
          fade: true
        },
        disabled: true,
        itemTemp: null,
        addContent: {
          id: '',
          comment: {commentId: '', commenterID: '', commenterName: '', commentContent: '', commentederID: '', commentederName: ''}
        },
        Cookie: {
          userid: '',
          name: ''
        },
        commentTemp: null,
        delShow: false
      }
    },
    mounted: function () {
      this.vueCookie()
      this.getContents()
    },
    methods: {
      handleSaveContent: function (contentsToBeSaved) {
        this.saveContents(contentsToBeSaved)
      },
      addContents () {
        var addUrl = 'http://localhost:3000/workText/comment/save'
        this.addContent.id = this.itemTemp.id
        this.addContent.comment.commentId = this.IdMake()
        this.addContent.comment.commenterID = this.Cookie.userid
        this.addContent.comment.commenterName = CookieName()
        this.addContent.comment.commentContent = this.commentContent
        this.addContent.comment.commentederID = this.itemTemp.userid
        this.addContent.comment.commentederName = this.itemTemp.author
        var Params = this.addContent
        var resource = this.$resource(addUrl)
        resource.save(addUrl, Params)
          .then((response) => {
            this.$Message.success('评论成功！')
            this.addDialogShow()
            this.getContents()
          })
      },
      deleteContent (item, commentId) {
        var delUrl = 'http://localhost:3000/workText/comment/remove'
        var resource = this.$resource(delUrl)
        var delParams = {workId: item.id, clickCommentId: commentId}
        resource.save(delUrl, delParams)
          .then((response) => {
            this.$Message.success('删除评论成功！')
            this.getContents()
          })
      },
      getContents () {
        var getUrl = 'http://localhost:3000/workText/get'
        var resource = this.$resource(getUrl)
        resource.get()
          .then((response) => {
            console.log(CookieUserType())
            if (CookieUserType() === '2') {
              for (var i = 0; i < response.body.length; i++) {
                if (response.body[i].IsPublic === '0') {
                  this.items.push(response.body[i])
                  this.items.reverse()
                }
              }
            } else {
              this.items = response.body
              this.items.reverse()
            }
          })
      },
      anwerContent () {
        var addUrl = 'http://localhost:3000/workText/comment/save'
        this.addContent.id = this.itemTemp.id
        this.addContent.comment.commentId = this.IdMake()
        this.addContent.comment.commenterID = this.Cookie.userid
        this.addContent.comment.commenterName = CookieName()
        this.addContent.comment.commentContent = this.commentContent
        this.addContent.comment.commentederID = this.commentTemp.commenterID
        this.addContent.comment.commentederName = this.commentTemp.commenterName
        var Params = this.addContent
        var resource = this.$resource(addUrl)
        resource.save(addUrl, Params)
          .then((response) => {
            this.$Message.success('评论成功！')
            this.answerDialogClose()
            this.getContents()
          })
      },
      selectInfo () {
        this.items = []
        var selectUrl = 'http://localhost:3000/workText/select' + '/'
        if (this.selectText === '') {
          selectUrl += this.Dic['userid']
        } else {
          selectUrl += this.selectText
        }
        var resource = this.$resource(selectUrl)
        resource.get()
          .then((response) => {
            this.items = response.body
            this.items.reverse()

            this.items = []
            if (CookieUserType() === '2') {
              for (var i = 0; i < response.body.length; i++) {
                if (this.Cookie.userid === response.body[i].userid) {
                  this.items.push(response.body[i])
                }
              }
              this.items.reverse()
            } else {
              console.log(response.body)
              this.items = response.body
              this.items.reverse()
            }
          })
      },
      showContent (item) {
        console.log(item)
        this.title = item.title
        this.createDate = item.createDateTime
        this.selected = item.IsPublic
        this.htmlForEditor = item.htmlForEditor
        this.author = item.author
        this.textID = item.textID
      },
      closeTipWind () {
        this.delClass.open = false
      },
      textIdMake () {
        var myDate = new Date()
        var temp = ''
        temp += myDate.getFullYear()
        temp += myDate.getMonth() + 1
        temp += myDate.getDate()
        temp += myDate.getHours()
        temp += myDate.getMinutes()
        temp += myDate.getSeconds()
        this.textID = temp
      },
      vueCookie () {
        this.Dic = getCookie()
        this.Cookie.userid = this.Dic['userid']
      },
      addDialogShow (item) {
        this.itemTemp = item
        this.addIsAbleShowClass.modal = false
        this.addIsAbleShowClass.fade = false
      },
      addDialogClose () {
        this.addIsAbleShowClass.modal = true
        this.addIsAbleShowClass.fade = true
      },
      answerDialogShow (tempComment, item) {
        this.watermark = '回复' + tempComment.commenterName + ':'
        this.commentTemp = tempComment
        this.itemTemp = item
        this.answerClass.modal = false
        this.answerClass.fade = false
      },
      answerDialogClose () {
        this.answerClass.modal = true
        this.answerClass.fade = true
      },
      IdMake () {
        var myDate = new Date()
        var temp = this.Cookie.userid + '&' + this.itemTemp.userid + '&'
        temp += myDate.getFullYear()
        temp += myDate.getMonth() + 1
        temp += myDate.getDate()
        temp += myDate.getHours()
        temp += myDate.getMinutes()
        temp += myDate.getSeconds()
        return temp
      },
      setUI () {
        for (var i = 0; this.items.length; i++) {
          this.Cookie.userid
        }
      }
    },
    watch: {
      commentContent () {
        if (this.commentContent === null || this.commentContent === undefined || this.commentContent === '') {
          this.disabled = true
        } else {
          this.disabled = false
        }
      }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .dialog{
    padding-top: 125px;
    overflow-y:scroll;
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
  }
</style>



